* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --main-color: #00acdc;
}

html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  left: 0;
  right: 0;
}

.logo {
  width: 40px;
  height: 40px;
}

.nav__list {
  color: #000;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  list-style: none;
  display: flex;
}

.nav__item {
  height: 100%;
  margin-left: 20px;
  text-decoration: none;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link {
  color: #000;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.is-active {
  color: var(--main-color);
  position: relative;
}

.nav .nav__link:hover {
  -webkit-text-decoration-color: var(--main-color);
  text-decoration-color: var(--main-color);
  color: var(--main-color);
}

.is-active:after {
  content: "";
  background-color: var(--main-color);
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
